<template>
  <Page
    x="c"
    y="s"
    :title="$t('form.register', 'alert.success')"
  >
    <Row class="p-lr-30 m-t-40 w-100b rup-header" x="sb" y="c">
      <img src="@/image/home/bifinanceMenu.png" class="w-158 h-42">
      <Row y="c" @click="showPicker = true">
        <Row class="font-28 bold">{{ columns.find((obj)=>obj.value===fieldValue)?.label }}</Row>
        <IconSvg name="icon-invite_select" class="font-20  m-t-4 m-l-8" />
      </Row>

      <van-popup v-model:show="showPicker" round position="bottom">
        <Row
          v-for="(item,index) in columns"
          :key="index"
          :class="index+1!==columns.length?'border-b':''"
          class="w-100b h-100 lang-item  font-32"
          x="c"
          @click="onConfirm(item)"
        >
          {{ item.label }}
        </Row>
        <Row class="h-10 w-100b bg-d" />
        <Row class="w-100b h-100 lang-item font-32 c-info" x="c" @click="showPicker=false">
          {{ $t('alert.cancel') }}
        </Row>
      </van-popup>
    </Row>

    <Column class="w-100b" x="c">
      <img src="@/image/user/registerUpLandSuccess2.webp" class="w-470 h-330 m-b-80 m-t-152">
      <Row x="c" class="font-42 bold  ">{{ $t('user.registered') }}</Row>
      <Row x="c" class="font-42 bold  ">{{ $t('user.campaignRewards') }}</Row>
      <Row x="c" class="label m-t-35 m-b-60">
        <i18n path="activity.chanceWinPrize">
          <template #n>
            <span class="c-primary m-lr-10">100</span>
            <span class="c-primary m-r-10">USDT</span>
          </template>
        </i18n>
      </Row>
      <van-button type="primary" class="w-420 h-88 radius-8 label bold" @click="toNewcomerBenefits">{{ $t('form.getRightNow') }}</van-button>
    </Column>
  </Page>
</template>

<script setup>
const languageStore = useLanguageStore()
const route = useRoute()

let fieldValue = $ref(languageStore.locale)
const showPicker = $ref(false)

const columns = $computed(() => {
  return languageStore.languageMap
})

const toNewcomerBenefits = () => {
  const query = reserveUrlQuery(route)
  router.push({
    path: '/activity/newcomerBenefits',
    query: {
      ...query,
    },
  })
}

const onConfirm = (item) => {
  // showPicker = false
  fieldValue = item.value
  localStorage.locale = item.value
  replaceParamVal('locale', item.value)
  location.reload()
}

onBeforeMount(() => {
})

</script>
<style lang="scss">
.rs-succ{
  height: calc(100vh - 358px);
}

.rs-succ{
  padding-top: 140px;
 }
 .rs-com-pic{
  margin-top:110px
 }

@media (max-width: 375px) {
 .rs-succ{
  padding-top: 40px;
 }
 .rs-com-pic{
  margin-top:30px
 }
}

</style>

